.wrapper-checklist {
  width: 100%;
  float: left;
}
.wrapper-checklist .header-list {
  width: 100%;
  margin: 0 0 0.8em 0;
}
.wrapper-checklist .header-list .editable-title {
  font-size: 0.9em;
}
.wrapper-checklist .header-list .editable-title:empty {
  color: rgba(7,10,18,0.32);
}
.wrapper-checklist .editable-item {
  width: 100%;
  position: relative;
  float: left;
  border-top: 1px solid rgba(7,10,18,0.12);
  border-bottom: 1px solid rgba(7,10,18,0.12);
  padding: 0.5em 1em 0.5em 1.8em;
  margin: 0.5em 0 0;
  font-size: 0.9em;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.wrapper-checklist .editable-item:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  width: 1.2em;
  height: 1.2em;
  border: 1px solid rgba(7,10,18,0.32);
  border-radius: 0.2em;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.wrapper-checklist .editable-item:empty {
  color: rgba(7,10,18,0.32);
}
.wrapper-checklist .checklist {
  width: 100%;
  float: left;
}
.wrapper-checklist .checklist .item {
  position: relative;
  width: 100%;
  float: left;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0 0 0.32em 0;
  padding: 0 0 0 1.3em;
  word-break: break-all;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.wrapper-checklist .checklist .item .button-delete {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  font-size: 0.6em;
  padding: 0.3em 1em;
  border-radius: 999px;
  background-color: rgba(7,10,18,0.1);
  color: rgba(7,10,18,0.62);
  font-family: 'pn-semibold';
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.wrapper-checklist .checklist .item .button-delete:hover {
  background-color: #f14960;
  color: #fff;
}
.wrapper-checklist .checklist .item:hover .button-delete {
  visibility: visible;
  opacity: 1;
}
.wrapper-checklist .checklist .item input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.wrapper-checklist .checklist .item .share-user {
  visibility: hidden;
  margin: 0 0 0 0.8em;
}
.wrapper-checklist .checklist .item .share-user .user-thumb .photo {
  width: 1.5em;
  height: 1.5em;
}
.wrapper-checklist .checklist .item .share-user .wrapper-share-user {
  width: 1.5em;
  height: 1.5em;
  margin: 0 0 0 0.5em;
}
.wrapper-checklist .checklist .item .share-user .wrapper-share-user .button-share-user {
  width: 1.5em;
  height: 1.5em;
}
.wrapper-checklist .checklist .item .icon-check {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  width: 1.2em;
  height: 1.2em;
  border: 1px solid rgba(7,10,18,0.32);
  border-radius: 0.1em;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border-radius: 0.2em;
  cursor: pointer;
}
.wrapper-checklist .checklist .item .text {
  font-size: 0.9em;
  margin: 0 0 0 0.5em;
  color: rgba(7,10,18,0.8);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-family: 'pn-semibold';
